.primary {
  color: #248700; }

.secondary {
  color: #4b286d; }

.inverted {
  color: #fff; }

.error {
  color: #c12335; }

.size16 {
  font-size: 1rem; }

.size24 {
  font-size: 1.5rem; }

.size48 {
  font-size: 3rem; }

.icon {
  font-family: 'TELUS Core Icons';
  display: inline-block;
  font-weight: normal;
  font-style: normal;
  speak: none;
  text-decoration: inherit;
  text-transform: none;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1;
  vertical-align: middle; }

.iconCaretDown {
  composes: icon; }
  .iconCaretDown::before {
    content: '\f105'; }

.iconCaretUp {
  composes: icon; }
  .iconCaretUp::before {
    content: '\f106'; }

.iconCheckmark {
  composes: icon; }
  .iconCheckmark::before {
    content: '\f101'; }

.iconChevron {
  composes: icon; }
  .iconChevron::before {
    content: '\f107'; }

.iconLeftChevron {
  composes: icon; }
  .iconLeftChevron::before {
    content: '\f107';
    display: inline-block;
    transform: rotate(-180deg) translateY(1.5px); }

