.item {
  position: relative; }
  .item::before {
    display: block;
    position: absolute;
    left: -24px;
    font-family: "TELUS Core Icons"; }

.circle {
  composes: item; }
  .circle::before {
    content: '\002022';
    color: #4b286d; }

.checkmark {
  composes: item; }
  .checkmark::before {
    content: "\f101";
    color: #248700; }

.x {
  composes: item; }
  .x::before {
    content: "\f104";
    color: #c12335; }
