.reset {
  composes: noSpacing from '../../src/components/Spacing.modules.scss';
  composes: none from '../../src/components/Borders.modules.scss'; }

.horizontal {
  composes: reset;
  width: 100%;
  height: 1px; }

.vertical {
  composes: reset;
  display: inline-block;
  width: 1px; }

.horizontalThin {
  composes: horizontal;
  background-color: #d8d8d8; }

.horizontalGradient {
  composes: horizontal;
  background-image: linear-gradient(90deg, rgba(216, 216, 216, 0) 0%, #d8d8d8 7%, #d8d8d8 93%, rgba(216, 216, 216, 0) 100%); }

.verticalThin {
  composes: vertical;
  background-color: #d8d8d8; }

.verticalGradient {
  composes: vertical;
  background-image: linear-gradient(0deg, rgba(216, 216, 216, 0) 0%, #d8d8d8 12%, #d8d8d8 88%, rgba(216, 216, 216, 0) 100%); }
