.underlineOnHover:link, .underlineOnHover:visited {
  text-decoration: underline; }

.underlineOnHover:hover {
  text-decoration: none; }

.base {
  composes: underlineOnHover;
  composes: focusOutline from '../../../shared/styles/Links.modules.scss'; }
  .base:link, .base:visited {
    color: #2a2c2e; }

.inverted {
  composes: underlineOnHover; }
  .inverted:link, .inverted:visited {
    color: #fff; }

.inheritColor {
  composes: underlineOnHover; }
  .inheritColor:link, .inheritColor:visited {
    color: inherit; }
