.base {
  composes: none rounded from '../../../src/components/Borders.modules.scss';
  composes: medium boldFont from '../../../src/components/Typography/Text/Text.modules.scss';
  composes: height font from '../../../src/components/Forms.modules.scss';
  margin: 0;
  padding: 0 2rem;
  cursor: pointer;
  background: none;
  transition: background 0.2s; }

.sizing {
  composes: base;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center; }
  @media (min-width: 48em) {
    .sizing {
      display: inline-flex;
      width: auto;
      min-width: 180px; } }

.inline + .inline {
  margin-left: 1rem; }

.primary {
  background-color: #248700;
  color: #fff; }
  .primary:hover {
    box-shadow: 0 0 0 1px;
    background-color: #fff;
    color: #248700; }

.secondary {
  background-color: #4b286d;
  color: #fff; }
  .secondary:hover {
    box-shadow: 0 0 0 1px;
    background-color: #fff;
    color: #4b286d; }

.inverted {
  background-color: #fff;
  color: #2a2c2e; }
  .inverted:hover {
    box-shadow: 0 0 0 1px;
    background-color: transparent;
    color: #fff; }

.centered {
  composes: rounded from '../../../src/components/Borders.modules.scss';
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center; }
