/* LABEL */
.alignCenter {
  align-items: center; }

/* END LABEL */
/* FIELD */
.formField {
  width: 100%;
  margin: 0;
  composes: verticalPadding-2 horizontalPadding-3 from '../../../packages/Box/Box.modules.scss';
  composes: thin rounded from '../../../src/components/Borders.modules.scss';
  composes: font from '../../../src/components/Forms.modules.scss';
  composes: medium mediumFont color from '../../../src/components/Typography/Text/Text.modules.scss';
  outline: 0;
  text-overflow: ellipsis; }
  .formField::placeholder {
    font: inherit;
    letter-spacing: inherit;
    line-height: inherit;
    color: #54595f; }

.defaultBoxShadow {
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.05); }

.defaultBgColor {
  background-color: #fff; }

.default {
  composes: formField defaultBgColor defaultBoxShadow;
  border-color: #54595f; }

.disabled {
  composes: formField;
  background-color: #f7f7f8;
  border-color: transparent; }

.focus {
  composes: formField defaultBgColor;
  border-color: transparent;
  box-shadow: 0 0 4px 1px #54595f; }

.success {
  composes: formField defaultBgColor defaultBoxShadow;
  border-color: #248700; }

.error {
  composes: formField defaultBgColor defaultBoxShadow;
  border-color: #c12335; }

/* END FIELD */
/* ICON */
.iconRight {
  right: 1rem; }

/* END ICON */
