.step-tracker {
  list-style: none;
  padding: 0;
  position: relative;
  display: flex;
  justify-content: space-between;
  margin: 1rem -1.5rem; }
  @media screen and (min-width: 768px) {
    .step-tracker {
      padding-bottom: 2.5rem;
      margin: 1rem 0; } }

.step-tracker__step {
  width: 100%;
  position: relative;
  text-align: center;
  height: 35px; }
  .step-tracker__step::before, .step-tracker__step::after {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: calc(50% - 1.07rem);
    content: ' ';
    border-bottom: 0.1rem solid #54595f; }
  .step-tracker__step::before {
    left: 0; }
  .step-tracker__step::after {
    right: 0; }
  .step-tracker__step--completed .step-tracker__icon {
    color: #fff;
    background-color: #248700; }
    .step-tracker__step--completed .step-tracker__icon::before {
      font-family: 'TELUS Core Icons';
      content: '\F101'; }
  .step-tracker__step--processing .step-tracker__icon {
    background-color: #248700; }
  .step-tracker__step--processing .step-tracker__label {
    color: #248700; }

.step-tracker__step:first-child::before {
  content: none; }

.step-tracker__step:last-child::after {
  content: none; }

.step-tracker__icon {
  display: inline-block;
  border: 0.1rem solid #248700;
  border-radius: 50%;
  font-size: 1.2rem;
  width: 35px;
  height: 35px; }
  .step-tracker__icon::before {
    font-family: 'TELUS Core Icons';
    line-height: 35px; }

.step-tracker__label {
  position: absolute;
  color: #2a2c2e;
  bottom: -3rem;
  left: 0;
  font-size: 1rem;
  width: 100%;
  height: 2.5rem;
  display: none; }
  @media screen and (min-width: 768px) {
    .step-tracker__label {
      display: inline; } }

.step-tracker__mobile-label {
  font-size: 1rem;
  width: 100%;
  text-align: center;
  color: #248700; }
  @media screen and (min-width: 768px) {
    .step-tracker__mobile-label {
      display: none; } }

.step-tracker__mobile-label-step-info {
  color: #2a2c2e;
  font-size: 1rem; }
