.sup {
  composes: baseSupSubScripts from '../Text/Text.modules.scss'; }

.base {
  composes: noSpacing from '../../Spacing.modules.scss';
  composes: wordBreak from '../../Typography.modules.scss';
  font-weight: 300; }

.heading {
  composes: base;
  font-size: 2.75rem;
  line-height: 1.14; }
  .heading .sup {
    font-size: 1.25rem;
    top: -1.2em; }
  @media (min-width: 48em) {
    .heading {
      font-size: 4.5rem;
      line-height: 1.11;
      letter-spacing: 0.2px; }
      .heading .sup {
        font-size: 1.25rem;
        top: -2.2em; } }

.inverted {
  color: #fff; }

.default {
  color: #4b286d; }
