.base {
  composes: focusOutline from '../../../../shared/styles/Links.modules.scss';
  text-decoration: none; }

.primary {
  composes: base; }
  .primary:link, .primary:visited {
    color: #fff; }
  .primary:hover {
    color: #248700; }

.secondary {
  composes: base; }
  .secondary:link, .secondary:visited {
    color: #fff; }
  .secondary:hover {
    color: #4b286d; }

.inverted {
  composes: base; }
  .inverted:link, .inverted:visited {
    color: #2a2c2e; }
  .inverted:hover {
    color: #fff; }
