.icon {
  font-family: 'TELUS Core Icons';
  display: inline-block;
  font-weight: normal;
  font-style: normal;
  speak: none;
  text-decoration: inherit;
  text-transform: none;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: color 0.1s linear; }

.icon-core-caret-up::before {
  content: '\f106'; }

.icon-core-caret-down::before {
  content: '\f105'; }

.selector-counter {
  position: relative;
  display: inline-block;
  width: 100%; }
  @media screen and (min-width: 768px) {
    .selector-counter {
      width: 11.125rem; } }
  @media screen and (min-width: 1200px) {
    .selector-counter {
      width: 9.875rem; } }
  .selector-counter__plain-button {
    margin: 0;
    padding: 0;
    border: 0;
    background-image: none;
    background-color: transparent;
    box-shadow: none;
    text-align: center; }
  .selector-counter__button {
    position: absolute;
    right: 15px;
    top: 1px;
    cursor: pointer;
    margin: 0;
    padding: 0;
    min-height: 0;
    min-width: auto;
    width: auto;
    background: transparent; }
    .selector-counter__button:last-child {
      top: 24px; }
    .selector-counter__button:disabled {
      background: transparent; }
  .selector-counter__icon {
    color: #248700;
    font-size: 11px;
    line-height: normal; }
    .selector-counter__icon:hover {
      color: #248700; }
    .selector-counter--error .selector-counter__icon,
    .selector-counter--error button:disabled .selector-counter__icon {
      color: #c12335; }
    .selector-counter--successful .selector-counter__icon,
    .selector-counter--successful button:disabled .selector-counter__icon {
      color: #248700; }
    button:disabled .selector-counter__icon {
      color: #54595f; }

input.selector-counter__value.selector-counter__value {
  font-weight: 300;
  font-size: 2.75rem;
  line-height: 2.75rem;
  padding: 0 35px 0 25px;
  height: 52px;
  max-width: 100%;
  -moz-appearance: textfield; }
  input.selector-counter__value.selector-counter__value::-webkit-inner-spin-button, input.selector-counter__value.selector-counter__value::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0; }
  .selector-counter--error input.selector-counter__value.selector-counter__value {
    border-color: #c12335; }
  .selector-counter--successful input.selector-counter__value.selector-counter__value {
    border-color: #248700; }

.field .selector-counter__value {
  margin-bottom: 18px; }

.selector-counter__value {
  border: 1px solid #54595f;
  border-radius: 4px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.05);
  color: #2a2c2e;
  font-family: "TELUS-Web", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1.25rem;
  letter-spacing: 0.4px;
  line-height: 1.4;
  outline: 0;
  padding: 11px;
  width: 100%;
  transition: background-color 0.1s linear; }

.accessible-hide {
  display: block;
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden; }
