.header {
  width: 100%;
  text-align: left; }

.headerAlign {
  flex: 1;
  align-items: flex-start; }

.disabled {
  background-color: #f7f7f8;
  cursor: default; }

@media (max-width: 47.99em) {
  .alignSelfFlexEnd {
    align-self: flex-end; } }

.showFromMd {
  display: none; }
  @media (min-width: 48em) {
    .showFromMd {
      display: inline-block; } }

.showUntilMd {
  display: inline-block; }
  @media (min-width: 48em) {
    .showUntilMd {
      display: none; } }
