.disabled {
  border-color: #d8d8d8;
  background-color: #d8d8d8; }

.checked {
  border-color: #54595f;
  background-color: #fff;
  cursor: pointer; }

.disabledChecked {
  border-color: #d8d8d8;
  background-color: #fff; }

.inner {
  composes: circular from '../Borders.modules.scss';
  height: 12px;
  width: 12px; }

.innerChecked {
  composes: inner;
  background-color: #248700; }

.innerDisabledChecked {
  composes: inner;
  background-color: #d8d8d8; }
