.link {
  composes: medium from '../../Typography/Text/Text.modules.scss';
  display: inline-block;
  text-decoration: none;
  max-width: 100%;
  font-weight: 500; }
  .link:hover .rightChevron {
    transform: translateX(0.25rem); }
  .link:hover .leftChevron {
    transform: translateX(-0.25rem); }

.primary {
  composes: link; }
  .primary:link, .primary:visited {
    color: #248700; }

.secondary {
  composes: link; }
  .secondary:link, .secondary:visited {
    color: #4b286d; }

.inverted {
  composes: link; }
  .inverted:link, .inverted:visited {
    color: #fff; }

.chevron {
  display: inline-block;
  transition: transform 300ms; }

.rightChevron {
  composes: chevron; }

.leftChevron {
  composes: chevron; }
