.bubble {
  composes: rounded from '../Borders.modules.scss';
  position: absolute;
  bottom: calc(100% + 10px);
  background-color: #fff;
  box-shadow: 0 0 2px 0 #2a2c2e, 0 3px 2px 0 rgba(84, 89, 95, 0.25); }
  .bubble::before {
    display: block;
    position: absolute;
    content: '';
    bottom: -7px;
    border-width: 7px;
    border-style: solid;
    border-color: transparent #fff #fff transparent;
    background-color: #fff;
    box-shadow: 2px 2px 3px 0 rgba(42, 42, 44, 0.4);
    transform: rotate(45deg); }

.left {
  composes: bubble;
  right: -7px; }
  .left::before {
    right: 12px; }

.right {
  composes: bubble;
  left: calc(100% - 24px - 7px); }
  .right::before {
    left: 12px; }

.full {
  width: 100%; }

.half {
  width: 50%; }

.quarter {
  width: 25%; }
