.spinner-wrapper {
  position: relative; }
  .spinner-wrapper .spinner {
    position: absolute;
    top: calc(50% - 50px);
    left: 0;
    width: 100%; }

.spinner-container {
  transition: opacity 0.3s; }
  .spinner-container--loading {
    opacity: 0.1;
    z-index: -1; }

.spinner-wrapper__content-blocker {
  z-index: 1400;
  position: absolute;
  width: 100%;
  height: 100%; }

.spinner {
  display: none;
  width: 100px;
  height: 100px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 50%;
  margin: auto;
  text-align: center; }
  .spinner--spinning {
    display: block; }
  .spinner--full-screen {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 1400; }

.spinner__full-screen-layer {
  background-color: #fff;
  opacity: 0.85;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed; }

.spinner__svg {
  z-index: 1600;
  animation-name: spinner-rotate;
  animation-duration: 1.8s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
  height: 100%;
  width: 100%;
  max-width: 100px;
  max-height: 100px; }

.spinner__circle {
  animation: spinner-dash 1.7s ease-in-out infinite 0s, spinner-color 6s ease-in-out infinite -0.75s; }

.spinner__tip {
  font-size: 0.8rem;
  text-align: center;
  position: relative;
  top: -1.8rem; }

@keyframes spinner-rotate {
  100% {
    transform: rotate(360deg); } }

@keyframes spinner-dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0; }
  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35; }
  100% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124; } }
