.alignCenter {
  align-items: center; }

.alignFlexStart {
  align-items: flex-start; }

.fakeInput {
  composes: thin from '../../../src/components/Borders.modules.scss';
  height: 20px;
  width: 20px;
  min-height: 20px;
  min-width: 20px;
  outline: 0;
  line-height: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-top: 0.125rem;
  transition: border-color 0.1s linear, background-color 0.1s linear; }

.fakeCheckbox {
  composes: fakeInput;
  composes: rounded from '../../../src/components/Borders.modules.scss'; }

.fakeRadio {
  composes: fakeInput;
  composes: circular from '../../../src/components/Borders.modules.scss'; }

.unchecked {
  border-color: #54595f;
  background-color: #fff;
  cursor: pointer; }
  .unchecked.focused {
    border-color: #fff; }

.error {
  border-color: #c12335;
  background-color: #fff;
  cursor: pointer; }

.focused {
  box-shadow: 0 0 4px 1px #54595f; }

.hiddenInput {
  width: 10px;
  height: 10px;
  padding: 0;
  margin: 0;
  opacity: 0;
  cursor: pointer;
  position: absolute;
  top: auto; }
